<script>
import { getContext } from 'svelte';

export let disabled = false;
export let label = 'Tab';

const { register, selected } = getContext( 'tabs' );

register( {
  label: label,
  disabled: disabled
} );
</script>

<style>
div {
  flex-grow: 1;
  margin: 0;
  padding: 16px;
}
</style>

<p>{console.log( $selected )}</p>

{#if $selected === label}

<div>
  <slot></slot>
</div>

{/if}
